import axios from "../utils/axios"
/**
 * 
 */
export function  getLunbo () {
 return axios({
            url:'/home/swiper'
        })
}
/**
 * 
 */
export function getZuFang (place) {
    return axios({
        url:'home/groups?area='+place
    })
}
// 
export function getHotMsg (id) {
    return axios({
        url:'home/news?area='+id
    })
}
/**
 * 
 */
export function getCityInfo (place) {
    return axios({
        url: 'area/info',
        params: {
            name:place
        }
    })
}