import React, { Component } from 'react'
import { Carousel, Flex, NavBar, Icon,Modal } from 'antd-mobile'
import HouseItem from '../../components/HouseItem'
import styles from './index.module.css'
import HousePackage from '../HousePackage'
import { baseurl } from "../../utils/axios"
// axios
import { getHouseItem } from "../../request/house"
// token
import {isAuth}from "../../utils/token"
// 
const recommendHouses = [
  {
    id: 1,
    src: baseurl + '/img/message/1.png',
    desc: '72.32/ /',
    title: ' 31',
    price: 4500,
    tags: ['']
  },
  {
    id: 2,
    src: baseurl + '/img/message/2.png',
    desc: '83//',
    title: ' 21',
    price: 7200,
    tags: ['']
  },
  {
    id: 3,
    src: baseurl + '/img/message/3.png',
    desc: '52//',
    title: '4 11',
    price: 4300,
    tags: ['']
  }
]

// // 
// const BMap = window.BMap

// const labelStyle = {
//   position: 'absolute',
//   zIndex: -7982820,
//   backgroundColor: 'rgb(238, 93, 91)',
//   color: 'rgb(255, 255, 255)',
//   height: 25,
//   padding: '5px 10px',
//   lineHeight: '14px',
//   borderRadius: 3,
//   boxShadow: 'rgb(204, 204, 204) 2px 2px 2px',
//   whiteSpace: 'nowrap',
//   fontSize: 12,
//   userSelect: 'none'
// }

const alert = Modal.alert

export default class HouseDetail extends Component {
  state = {
    // 
    isLoading: false,

    // 
    houseInfo: {
      // 
      houseImg: [],
      // 
      title: '',
      // 
      tags: [],
      // 
      price: 0,
      // 
      roomType: '',
      // 
      size: 0,
      // 
      oriented: [],
      // 
      floor: '',
      // 
      community: '',
      // 
      coord: {
        latitude: '39.928033',
        longitude: '116.529466'
      },
      // 
      supporting: [],
      // 
      houseCode: '',
      // 
      description: ''
    },

    // 
    isFavorite: false
  }

  componentDidMount() {
    // 
    // 
    this.getHouseDetail()
  }
  // 
  handleFavorite = () => {
    // 
    if (isAuth('token')) {
      //
      // 
    } else {
      // 
      // 
      alert('', '?', [
        { text: '', onPress: () =>{}},
        {
          text: '', onPress: () =>
            //,
            this.props.history.replace({ pathname: '/login', historyName: this.props.match.params.id })
        },
      ]);


    }
  }
  /*
      

      1  handleFavorite 
      2  isAuth 
      3  Modal.alert 
      4 
      5  state

      6  isFavorite 
      7 
      8 

      alert('', '?', [
        { text: '' },
        {
          text: '',
          onPress: () => {}
        }
      ])
    */

  // 
  async getHouseDetail () {

    const { id } = this.props.match.params

    // loading
    this.setState({
      isLoading: true
    })
    const res=await getHouseItem(id)

    this.setState({
      houseInfo: res.body,
      isLoading: false
    })

    const { community, coord } = res.body

    // 
    this.renderMap(community, coord)
  }

  // 
  renderSwipers() {
    const {
      houseInfo: { houseImg }
    } = this.state

    return houseImg.map(item => (
      <a key={item} href="http://www.baidu.cn">
        <img src={baseurl + item} alt="" />
      </a>
    ))
  }

  // 
  renderMap(community, coord) {
    // const { latitude, longitude } = coord

    // const map = new BMap.Map('map')
    // const point = new BMap.Point(longitude, latitude)
    // map.centerAndZoom(point, 17)

    // const label = new BMap.Label('', {
    //   position: point,
    //   offset: new BMap.Size(0, -36)
    // })

    // label.setStyle(labelStyle)
    // label.setContent(`
    //     <span>${community}</span>
    //     <div class="${styles.mapArrow}"></div>
    //   `)
    // map.addOverlay(label)
  }

  // 
  renderTags() {
    const {
      houseInfo: { tags }
    } = this.state

    return tags.map((item, index) => {
      // 3
      let tagClass = ''
      if (index > 2) {
        tagClass = 'tag3'
      } else {
        tagClass = 'tag' + (index + 1)
      }

      return (
        <span key={item} className={[styles.tag, styles[tagClass]].join(' ')}>
          {item}
        </span>
      )
    })
  }
  // 
  targetBack = () => {
  }
  render() {
    const {
      isLoading,
      houseInfo: {
        community = '',
        title,
        price,
        roomType,
        size,
        floor,
        oriented,
        supporting,
        description
      },
      isFavorite
    } = this.state
    return (
      <div className={styles.root}>
        {/*  */}
        <NavBar
          mode="dark"
          icon={<Icon type="left" />}
          onLeftClick={() => { this.props.history.goBack()}}
          rightContent={[<i key="share" className="iconfont icon-share" />]}
        >
          
        </NavBar>
        {/*  */}
        <div className={styles.slides}>
          {!isLoading ? (
            <Carousel autoplay infinite autoplayInterval={5000}>
              {this.renderSwipers()}
            </Carousel>
          ) : (
              ''
            )}
        </div>
        {/*  */}
        <div className={styles.info}>
          <h3 className={styles.infoTitle}>{title}</h3>
          <Flex className={styles.tags}>
            <Flex.Item>{this.renderTags()}</Flex.Item>
          </Flex>

          <Flex className={styles.infoPrice}>
            <Flex.Item className={styles.infoPriceItem}>
              <div>
                {price}
                <span className={styles.month}>/</span>
              </div>
              <div></div>
            </Flex.Item>
            <Flex.Item className={styles.infoPriceItem}>
              <div>{roomType}</div>
              <div></div>
            </Flex.Item>
            <Flex.Item className={styles.infoPriceItem}>
              <div>{size}</div>
              <div></div>
            </Flex.Item>
          </Flex>

          <Flex className={styles.infoBasic} align="start">
            <Flex.Item>
              <div>
                <span className={styles.title}></span>
                
              </div>
              <div>
                <span className={styles.title}></span>
                {floor}
              </div>
            </Flex.Item>
            <Flex.Item>
              <div>
                <span className={styles.title}></span>
                {oriented.join('')}
              </div>
              <div>
                <span className={styles.title}></span>
              </div>
            </Flex.Item>
          </Flex>
        </div>
        {/*  */}
        <div className={styles.map}>
          <div className={styles.mapTitle}>
            
            <span>{community}</span>
          </div>
          <div className={styles.mapContainer} id="map">
            
          </div>
        </div>
        {/*  */}
        <div className={styles.about}>
          <div className={styles.houseTitle}></div>
          {/* <HousePackage list={supporting} /> */}
          {/* <div className="title-empty"></div> */}

          {supporting.length === 0 ? (
            <div className={styles.titleEmpty}></div>
          ) : (
              <HousePackage list={supporting} />
            )}
        </div>
        {/*  */}
        <div className={styles.set}>
          <div className={styles.houseTitle}></div>
          <div>
            <div className={styles.contact}>
              <div className={styles.user}>
                <img src={baseurl + '/img/avatar.png'} alt="" />
                <div className={styles.useInfo}>
                  <div></div>
                  <div className={styles.userAuth}>
                    <i className="iconfont icon-auth" />
                    
                  </div>
                </div>
              </div>
              <span className={styles.userMsg}></span>
            </div>

            <div className={styles.descText}>
              {description || ''}
            </div>
          </div>
        </div>
        {/*  */}
        <div className={styles.recommend}>
          <div className={styles.houseTitle}></div>
          <div className={styles.items}>
            {recommendHouses.map(item => (
              <HouseItem {...item} key={item.id} />
            ))}
          </div>
        </div>
        {/*  */}
        <Flex className={styles.fixedBottom}>
          <Flex.Item onClick={this.handleFavorite}>
            <img
              src={
                baseurl + (isFavorite ? '/img/star.png' : '/img/unstar.png')
              }
              className={styles.favoriteImg}
              alt=""
            />
            <span className={styles.favorite}>
              {isFavorite ? '' : ''}
            </span>
          </Flex.Item>
          <Flex.Item></Flex.Item>
          <Flex.Item>
            <a href="tel:400-618-4000" className={styles.telephone}>
              
            </a>
          </Flex.Item>
        </Flex>
      </div>
    )
  }
}
