import React, { Component } from 'react'
import { Flex, WingBlank, WhiteSpace, NavBar, Toast } from 'antd-mobile'

import { Link } from 'react-router-dom'

import styles from './index.module.css'
import { getLogin } from "../../request/login"
import {setToken} from"../../utils/token"
import { withFormik } from 'formik';
import * as Yup from 'yup';
// 
const REG_UNAME = /^[a-zA-Z_\d]{5,8}$/
const REG_PWD = /^[a-zA-Z_\d]{5,12}$/

class Login extends Component {
  state = {
    username:'',
    password:''
  }

  // axios
  render() {
    const {
      values,
      errors,
      handleChange,
      handleSubmit,
    } = this.props;

    return (
      <div className={styles.root}>
        {/*  */}
        <NavBar mode="light">
          
        </NavBar>
        <WhiteSpace size="xl" />

        {/*  */}
        <WingBlank>
          <form onSubmit={handleSubmit}>
            <div className={styles.formItem}>
              <input
                value={values.username}
                onChange={handleChange}
                className={styles.input}
                name="username"
                placeholder=""
              />
            </div>
            {/* 58 */}
            {errors.username && <div className={styles.error}>{errors.username}</div>}
            <div className={styles.formItem}>
              <input
                value={values.password}
                className={styles.input}
                name="password"
                type="password"
                onChange={handleChange}
                placeholder=""
              />
            </div>
            {/* 512 */}
            {errors.password && <div className={styles.error}>{errors.password}</div>}
            <div className={styles.formSubmit}>
              <button className={styles.submit} type="submit">
                 
              </button>
            </div>
          </form>
          <Flex className={styles.backHome}>
            <Flex.Item>
              <Link to="/registe">~</Link>
            </Flex.Item>
          </Flex>
        </WingBlank>
      </div>

    )
  }
}

export default  withFormik({
  mapPropsToValues: () => ({ username: '',password:'' }),
  validationSchema: Yup.object().shape({
    username: Yup.string().required('').matches(REG_UNAME, '58'),
    password: Yup.string().required('').matches(REG_PWD, '512'),
  }),
  // Custom sync validation
  // validate: values => {
  //   const errors = {};

  //   if (!values.username) {
  //     errors.username = 'Required';
  //   }

  //   return errors;
  // },

  handleSubmit: async(value,formikBag) => {
  //
    // 
    // 
    console.log(formikBag.props);

    const { status,description, body } = await getLogin(value)
    // 
    if (status === 200) {
      setToken('token',body.token)
      Toast.success(description, 2)
      // formikBagprops
      // 
      const { historyName } = formikBag.props.location
      // 
      if (historyName) {
        formikBag.props.history.push('detail/'+historyName)
      } else {
        // user
        formikBag.props.history.push('/home/user')
      }

    } else {
      Toast.fail(description)
    }

  },
})(Login);

