import React, { Component } from 'react'
// 
import { Carousel ,Flex,Grid,WingBlank,SearchBar} from 'antd-mobile';
//axios
import { getLunbo, getZuFang,getHotMsg } from "../../request/home"
// 
import { baseurl } from "../../utils/axios"
// 
import "./index.scss"
// 
import navArr from "../../utils/navList"
import {getCurrCity}from"../../utils/getCity"
export default class HomeIndex extends Component {
    state = {
        // 
        keyword:'',
        //
        data: [],
        imgHeight: 176,
        // 
        statusLunBo: false,
        // 
        place: {},
        // 
        ZuFArr: [],
        //
        hotArr:[]
    }
    // 
    lunBo () {
        return (
            <div>
            <Carousel
            autoplay={this.state.statusLunBo}
            infinite
            >
            {this.state.data.map(val => (
                <a
                key={val.id}
                href="http://www.alipay.com"
                style={{ display: 'block', width: '100%', height: this.state.imgHeight }}
                >
                <img
                    src={baseurl+val.imgSrc}
                    alt=""
                    style={{ width: '100%', verticalAlign: 'top' }}
                    onLoad={() => {
                    // fire window resize event to change height
                    window.dispatchEvent(new Event('resize'));
                    this.setState({ imgHeight: 'auto' });
                    }}
                />
                </a>
            ))}
            </Carousel>
        </div>

        )
    }
    // 
    Nav () {
        return (
            <div>
                <Flex className="nav">
                    {
                        navArr.map(i => {
                            return (
                            <Flex.Item  key={i.id}>
                                 <img src={i.img} onClick={()=>{this.props.history.push(i.path)}} alt=""/>
                                 <p>{i.title}</p>
                            </Flex.Item>
                            )
                        })
                    }
            </Flex>
            </div>
        )
    }

    // 
    More () {
      return(
        <div className="group">
            <Flex className="group-title" justify="between">
            <h3></h3>
            <span></span>
            </Flex>
              {/*  */}
              <Grid
                    data={this.state.ZuFArr}
                    columnNum={2}
                    hasLine={false}
                    square={false}
                    renderItem={item => (
                    <div style={{  }}>
                            {/* flex */}
                            <Flex className="grid-item" justify="between">
                            <div className="desc">
                                    <h3>{item.title}</h3>
                                    <p>{item.desc}</p>
                            </div>
                            <img src={baseurl+item.imgSrc} alt="" />
                            </Flex>
                    </div>
                    )}
                    />
        </div>
      )
    }

    // 
    HotNews () {
        return (
            <div className="news">
            <h3 className="group-title"></h3>
                <WingBlank size="md">
                    {
                              this.state.hotArr.map(item => (
                                <div className="news-item" key={item.id}>
                                  <div className="imgwrap">
                                    <img
                                      className="img"
                                      src={baseurl+item.imgSrc}
                                      alt=""
                                    />
                                  </div>
                                  <Flex className="content" direction="column" justify="between">
                                    <h3 className="title">{item.title}</h3>
                                    <Flex className="info" justify="between">
                                      <span>{item.from}</span>
                                      <span>{item.date}</span>
                                    </Flex>
                                  </Flex>
                                </div>
                              ))
                    }
                </WingBlank>
            </div>
        )
    }
    // 
    Search =()=> {
        return (
            <div className="search">
           <Flex justify="around" className="topNav">
                    <div className="searchBox">
                    <div className="city" onClick={()=>{this.props.history.push('/city')}}>
                            {this.state.place.label}<i className="iconfont icon-arrow" />
                    </div>
                    <SearchBar
                        value={this.state.keyword}

                        onChange={(v) => this.setState({ keyword: v })}
                        placeholder=""
                    />
                    </div>
                    <div className="map">
                    <i key="0" className="iconfont icon-map" />
                    </div>
                </Flex>
            </div>
        )
    }
    //
    // 
    getCityId = async() => {
        const data = await getCurrCity()
        console.log(data);
        // 
        this.setState({
            place:data
        })


  }
     // 
    async componentDidMount () {
       this.getCityId()
    //    promise.all
         const data=await Promise.all([ // simulate img loading
            // 
             getLunbo(),
               //
             getZuFang('AREA|88cff55c-aaa4-e2e08787878'),
            //
            getHotMsg('AREA|88cff55c-aaa4-e2e0'),

         ])
        //,,200
        if (data[0].status === 200) {
            // 
            this.setState({
                // 
                data: data[0].body,
                // 
                ZuFArr: data[1].body,
                // 
                hotArr: data[2].body,
                statusLunBo:true
            })
        }

    }
    render() {
        return (
            <div>
                <div className="header">
                    {/*  */}
                    {this.Search()}
                    {/*  */}
                    {this.lunBo()}
                    {/*  */}
                    {this.Nav()}
                    {/*  */}
                    {this.More()}
                    {/*  */}
                    {this.HotNews()}
                </div>
         </div>
        )
    }
}
